.contacts {
  background-color: collection-color("main-background");

  .container {
    @include background-pos(no-repeat, left 87px left bottom, auto);
    @include for-size(desktop) {
      background-image: url(../images/contacts/desktop/bg.png);
      @media screen and (min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
        background-image: url(../images/contacts/desktop/bg@2x.png);
      }
    }
  }

  // .contacts__title
  &__title {
    padding-top: 60px;
    text-align: center;

    font-family: 'Inika', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: calc(52 / 40);
    color: collection-color("main-color");
  }

  // .contacts__row
  &__row {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    padding-top: 37px;
  }

  &__list {
    position: relative;
    overflow: hidden;
    display: flex;
    min-width: 450px;
    padding-bottom: 229px;

    .dropdown__label {
      z-index: 1;
      min-width: 450px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: #D6E7D2;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 0px;
      font-family: 'Inter', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: calc(40 / 16);
      padding: 5px 35px;
      color: #499A18;
      cursor: pointer;

      &:hover {
        background: #C1E698;

        .dropdown {
          transform: translateY(0);
        }
      }
    }
  }

  // .contacts__dropdown

  &__dropdown {
  }
}

.dropdown {
  width: 450px;
  position: absolute;
  top: 45px;
  left: 0;
  background: #D6E7D2;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: inherit;
  border-radius: 0px;
  padding: 27px 41px 31px 41px;

  transform: translatey(-104%);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  // .dropdown__item
  &__item {
    border: inherit;
    border-bottom: 1px solid #D9D9D9;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: calc(40 / 16);
    padding: 5px 35px;
    color: #499A18;
    cursor: pointer;

    &:hover {
      color: #717171;
      border-bottom: 1px solid #717171;
    }
  }
}

